
import {defineComponent, ref} from "vue";


export default defineComponent({
    name: "CustomSelect",
    props: {
        selectOptionInput: {
            type: Array
        },
        targetString: {
            type: String
        },
        allowAll: {
            type: Boolean
        },
        fixedLeft: {
            type: Boolean
        }
    },
    data() {
        return {
            options: <any>[],
            left: 0,
            selected: []
        }
    },
    mounted() {
        this.left = (this.$refs as any).selectRef.getClientRects()[0].left;
        this.options = this.selectOptionInput!;
        // console.log('options', this.options)

        let containSelected = false;
        // console.log('this.options.find((option: any) => option.isSelected)', this.options.find((option: any) => option.isSelected))
        if (this.options.find((option: any) => option.isSelected)) {
            containSelected = true;
        }

        if (this.allowAll) {
            const allOption = {
                commonName: 'All',
                isAll: true,
                isSelected: !containSelected
            }
            this.options.unshift(allOption);
        }
    },

    methods: {
        getSelectedOptions() {
            let selectedOptions = this.options.filter((option: any) => option.isSelected)
            let finalOutput = ""
            selectedOptions.map((option: any, index: number) => {
                if (index > 0) {
                    finalOutput = finalOutput + ', ' + option[(this.targetString as any)]
                } else {
                    finalOutput = option[(this.targetString as any)]
                }

            })
            return finalOutput
        },

        getLeft() {
            if (this.fixedLeft) {
                return { 'left': this.left + 'px'}
            } else {
                return { 'position': 'absolute'}
            }

        },
        selectOptionAction(option: any) {
            if (option.isAll && option.isSelected == false) {
                this.options.forEach((val: any, index: number) => {
                    if (index > 0) {
                        val.isSelected = false;
                    }
                })
            } else if (!option.isAll && this.options[0].isAll) {
                this.options[0].isSelected = false;
            }
            option.isSelected = !option.isSelected
        }
    }

})
