<template>
    <div> <!--  id = app -->
        <!--    <v-app>-->
              <dashboard :enable-by-config="true"
                         config-url="/Config/Dashboard" info-url="/Auth/GetInfo" logout-url="/Auth/Logout">
        <!--          <schedules-navigation></schedules-navigation>-->
        <!--          <alert></alert>-->
        <!--          <div>-->
        <!--            <router-view></router-view>-->
        <!--          </div>-->
            <div class="v-application">
                <schedules-navigation></schedules-navigation>
                <alert></alert>

                <router-view/>
            </div>

              </dashboard>
        <!--    </v-app>-->



    </div>
</template>

<script>
import './App.scss'
import schedulesNavigation from "./components/schedulesNavigation/SchedulesNavigation";
import Alert from './components/alert'
import Dashboard from '../external-lib/dashboard'
import {useStore} from "vuex";
import {FlexScheduleMutation} from "@/store/flexScheduleModule";
import {isMobile} from "@/components/common/script";

export default {
    name: 'App',

    components: {
        Dashboard,
        schedulesNavigation,
        Alert,
    },

    setup() {
        const store = useStore()

        if (isMobile()) {
            store.commit(FlexScheduleMutation.setConfigAllTrue)
        } else {
            setLocalStorage();
        }

        function setLocalStorage() {
            let clientStorageVersion = localStorage.getItem('clientStorageVersion')
            if (clientStorageVersion !== '0.11') {
                localStorage.clear();
                localStorage.setItem('clientStorageVersion', '0.11')
                localStorage.setItem('flexScheduleCellSetting', JSON.stringify(store.state.FlexSchedule.flexScheduleCellSetting))
            } else {
                let storeCellSetting = store.state.FlexSchedule.flexScheduleCellSetting
                let localSetting = JSON.parse(localStorage.getItem('flexScheduleCellSetting'))
                for (let key of Object.keys(localSetting)) {
                    if (storeCellSetting[key]) {
                        storeCellSetting[key] = localSetting[key]
                    }
                }

                // this.$store.state.FlexSchedule.flexScheduleCellSetting = JSON.parse(localStorage.getItem('flexScheduleCellSetting'))
            }
        }

        // todo: turn localstorage into a module or plugin
    },

    mounted() {
        console.log('app vue created')
    }
};
</script>
<style>
.v-application {
    min-height: calc(100vh - 130px);
}

.v-application .v-btn:not(.v-btn--round).v-size--small {
    min-width: auto;
}
</style>