
import 'chartjs-adapter-moment';

import Modal from "@/components/common/Modal.vue";

import {defineComponent} from "vue";
import moment, {Moment} from "moment";
import Remote from "@/remote/scheduleRequest";
import {EmployeeGetters} from "@/store/employeeModule";
import Dialog from "@/components/dialog";
import MultiSelect from "@/components/common/MultiSelect.vue";
import {entries} from "lodash";

declare var Affinity: any

export interface CostDay {
    scheduled: number;
    budgeted: number;
    actual: number;
    sales: number;
    date: string;
    weekDay: string;
    budgetedVSScheduled: number;
    // salesVsBudgeted: number;
    salesVSScheduled: number;
    salesVSBudgeted: number;
    budgetedVSActuals: number;
    actualsVSScheduled: number;
    salesVSActuals: number;
}

export default defineComponent ({
    name: "CostAnalysisDialog",
    components: {
        Modal,
        MultiSelect,
    },
    props: {
        dayFocus: {
            type: Object
        },
        modalIndex: {
            type: Number
        }
    },
    data() {
        return {
            selectedDate: <Moment | null> null,
            data: <any[]>[],
            total: <any>{},
            displayN: 6, // number of column to display,

            displayList: <any>{},
            sourceDisplayList: <any>{
                scheduled: {
                    heading: 'Scheduled',
                    isDisplay: true,
                    isCalculated: false,
                },
                actual: {
                    heading: 'Actuals',
                    isDisplay: false,
                    isCalculated: false,
                },
                budgeted: {
                    heading: 'Budgeted',
                    isDisplay: false,
                    isCalculated: false,
                },
                sales: {
                    heading: 'Sales',
                    isDisplay: false,
                    isCalculated: false,
                },
                budgetedVSScheduled: {
                    heading: 'Budgeted vs Scheduled',
                    isDisplay: false,
                    isCalculated: true,
                },
                salesVSBudgeted: {
                    heading: 'Sales vs Budgeted',
                    isDisplay: false,
                    isCalculated: true,
                },
                budgetedVSActuals: {
                    heading: 'Budgeted vs Actuals',
                    isDisplay: false,
                    isCalculated: true,
                },
                actualsVSScheduled: {
                    heading: 'Actuals vs Scheduled',
                    isDisplay: false,
                    isCalculated: true,
                },
                salesVSScheduled: {
                    heading: 'Sales vs Scheduled',
                    isDisplay: false,
                    isCalculated: true,
                },
                salesVSActuals: {
                    heading: 'Sales vs Actuals',
                    isDisplay: false,
                    isCalculated: true,
                },
            },
          
            weekStart: '',
            weekEnd: '',
            displayOptions: [
                {
                    key: 'scheduled',
                    description: 'Scheduled',
                    isSelected: true,
                },
                {
                    key: 'actual',
                    description: 'Actuals',
                    isSelected: false,
                },
                {
                    key: 'budgeted',
                    description: 'Budgeted',
                    isSelected: true,
                },
                {
                    key: 'sales',
                    description: 'Sales',
                    isSelected: true,
                },
                {
                    key: 'budgetedVSScheduled',
                    description: 'Budgeted vs Scheduled',
                    isSelected: false,
                },
                {
                    key: 'salesVSBudgeted',
                    description: 'Sales vs Budgeted',
                    isSelected: false,
                },
                {
                    key: 'budgetedVSActuals',
                    description: 'Budgeted vs Actuals',
                    isSelected: false,
                },
                {
                    key: 'actualsVSScheduled',
                    description: 'Actuals vs Scheduled',
                    isSelected: false,
                },
                {
                    key: 'salesVSScheduled',
                    description: 'Sales vs Scheduled',
                    isSelected: false,
                },
                {
                    key: 'salesVSActuals',
                    description: 'Sales vs Actuals',
                    isSelected: false,
                },
            ]
        }
    },

    setup() {
        //
    },

    computed: {
        comparedValue(): any {
            let returnList = []
            for (let [key, value] of Object.entries(this.displayList)) {
                if ((value as any).isDisplay) {
                    returnList.push(value)
                }
            }
            return returnList
        }
    },
    mounted() {
        const localSetting = JSON.parse(localStorage.getItem('costAnalysisSetting_v1.0')!);

        if (localSetting) {
            this.displayOptions = localSetting;
        }

    },
    watch: {
        displayOptions: {
            handler(val, oldVal) {
                this.displayList = {};
                for (const [key, value] of Object.entries(this.sourceDisplayList)) {
                    const found = val.find((v: any) => v.key === key);
                    if (found && found.isSelected) {
                        this.displayList[key] = value;
                        this.displayList[key].isDisplay = found.isSelected;
                    } else {
                        console.error('key not found', key)
                    }

                    // if (found) {

                    // }
                }
                localStorage.setItem('costAnalysisSetting_v1.0', JSON.stringify(val))
            },
            deep: true
        },
    },

    methods: {
        getDataStyle(day: any, key: any, value: any) {
            if (!value.isCalculated) {
                return;
            }
            return {
                'positive-cell': day[key] > 0,
                'negative-cell': day[key] < 0
            }
        },

        getGridTemplateStyle() {
            this.calculateDisplayRow();
            if (this.displayN > 1) {
                return `grid-template-columns: 100px repeat( ${this.displayN} , 100px)`;
            } else {
                return `grid-template-columns: 100px repeat( ${this.displayN} , auto)`;
            }

        },
        open() {

            // this.displayList = {
            //     scheduled: false,
            //     budgeted: false,
            //     sales: false,
            //     // salesVsBudgeted: false,
            //     budgetedVSScheduled: false,
            //     salesVSScheduled: false,
            // }

            Object.keys(this.displayList).forEach((v: any) => {
                this.displayList[v].isDisplay = false
            })


            let weekStart = (this.dayFocus as any).clone().startOf('week').format("DD/MMM/YYYY");
            this.weekStart = weekStart;
            let weekEnd =  (this.dayFocus as any).clone().endOf('week').format("DD/MMM/YYYY");
            this.weekEnd = weekEnd;
            let employeeNumbers = this.$store.getters[EmployeeGetters.getEmployees].map((emp: any) => {
                return emp.employeeNo
            }) ;

            let data = {
                employeeNumbers: employeeNumbers,
                dateFrom: weekStart,
                dateTo: weekEnd
            }

            Dialog.loading()

            for (const [key, value] of Object.entries(this.sourceDisplayList)) {
                const found: any = this.displayOptions.find((v: any) => v.key === key);
                if (found && found.isSelected === true) {
                    this.displayList[key] = value;
                    this.displayList[key].isDisplay = found.isSelected;
                } else {
                    console.error('key not found', key)
                }

                // if (found) {

                // }
            }

            Remote.GetCostAnalysis(data).then(res => {

                if (!res[0]) {
                    this.data = [];
                    this.total = [];
                    (this.$refs.ModalRef as any).toggleShow();
                    Dialog.clear();
                    return;
                }

                let costDays = res.map((element: any) => {
                    return Object.fromEntries(
                        Object.entries(element).map(([k,v]) => [k.charAt(0).toLowerCase() + k.slice(1), v])
                    );
                })

                // this.calculateDisplayRow(costDays);

                this.data = costDays.map((costDay: CostDay) => {
                    let newDay: CostDay = {...costDay};

                    newDay.weekDay = moment(costDay.date).format('dddd');

                    newDay.sales = Math.round(costDay.sales * 100) / 100;
                    newDay.budgeted = Math.round(costDay.budgeted * 100) / 100;
                    newDay.actual = Math.round(costDay.actual * 100) / 100;
                    newDay.scheduled = Math.round(costDay.scheduled * 100) / 100;

                    if (costDay.budgeted && costDay.scheduled) {
                        newDay.budgetedVSScheduled = Math.round((costDay.budgeted - costDay.scheduled) / costDay.scheduled * 10000) / 100
                    }

                    if (costDay.budgeted && costDay.sales) {
                        newDay.salesVSBudgeted = Math.round((costDay.sales - costDay.budgeted) / costDay.budgeted * 10000) / 100
                    }

                    if (costDay.budgeted && costDay.actual) {
                        newDay.budgetedVSActuals = Math.round((costDay.budgeted - costDay.actual) / costDay.actual * 10000) / 100
                    }

                    if (costDay.actual && costDay.scheduled) {
                        newDay.actualsVSScheduled = Math.round((costDay.actual - costDay.scheduled) / costDay.scheduled * 10000) / 100
                    }
                    if (costDay.sales && costDay.actual) {
                        newDay.salesVSActuals = Math.round((costDay.sales - costDay.actual) / costDay.actual * 10000) / 100
                    }

                    if (costDay.sales && costDay.scheduled) {
                        newDay.salesVSScheduled = Math.round((costDay.sales - costDay.scheduled) / costDay.scheduled * 10000) / 100
                    }

                    for (let [key, val] of Object.entries(newDay)) {
                        if (Number.isNaN(val)) {
                            (newDay as any)[key] = 0;
                        }
                    }

                    return newDay;
                })

                this.calculateTotal();

                Dialog.clear();
                (this.$refs.ModalRef as any).toggleShow();

            }).catch(err => {
                this.data = [];
                this.total = [];
            })
        },

        calculateTotal() {
            const keyList = []

            for (const [key, value] of Object.entries(this.sourceDisplayList)) {
                // if (!(value as any).isCalculated && (value as any).isDisplay) {
                keyList.push(key)
                // }
            }

            if (keyList.length === 1) {
                this.total[keyList[0]] = Math.round(this.data[0][keyList[0]] * 100) / 100;
                return;
            }

            for (let k of keyList) {
                if (this.data[0][k] === 0) {
                    if (this.data[1] && this.data[1][k] === 0) {
                        continue;
                    }
                }

                if (this.data.length === 1) {
                    this.total[k] = Math.round(this.data[0][k] * 100) / 100;
                    continue;
                }

                if (this.data[0] && this.data[1]) {
                    this.total[k] = this.data.reduce((pre: any, next: any) => {
                        if (typeof pre === 'object') {
                            return pre[k] + next[k]
                        }
                        return pre + next[k]
                    });
                    this.total[k] = Math.round(this.total[k] * 100) / 100;
                } else {

                    // this.total[k] = Math.round(this.total[k] * 10000) / 100;
                }
            }

            if (this.sourceDisplayList.budgetedVSScheduled) {
                this.total.budgetedVSScheduled = Math.round((this.total.budgeted - this.total.scheduled) / this.total.scheduled * 10000) / 100;

            }
            if (this.sourceDisplayList.salesVSBudgeted) {
                this.total.salesVSBudgeted = Math.round((this.total.sales - this.total.budgeted) / this.total.budgeted * 10000) / 100;
            }
            if (this.sourceDisplayList.budgetedVSActuals) {
                this.total.budgetedVSActuals = Math.round((this.total.budgeted - this.total.actual) / this.total.actual * 10000) / 100;
            }
            if (this.sourceDisplayList.actualsVSScheduled) {
                this.total.actualsVSScheduled = Math.round((this.total.actual - this.total.scheduled) / this.total.scheduled * 10000) / 100;
            }
            if (this.sourceDisplayList.salesVSScheduled) {
                this.total.salesVSScheduled = Math.round((this.total.sales - this.total.scheduled) / this.total.scheduled * 10000) / 100;
            }
            if (this.sourceDisplayList.salesVSActuals) {
                this.total.salesVSActuals = Math.round((this.total.sales - this.total.actual) / this.total.actual * 10000) / 100;
            }

            for (let [key, val] of Object.entries(this.total)) {
                if (Number.isNaN(val)) {
                    this.total[key] = 0;
                }
            }
        },

        calculateDisplayRow() {
            // console.log('day', days)
            // let displayDef = 1;
            this.displayN = this.displayOptions.filter((option: any) => option.isSelected ).length

            // this.displayN = displayDef;


            // for (let day of days) {
            //     let displayN = 1;
            //
            //     for (let key of Object.keys(this.displayList)) {
            //         console.log('day[key as keyof CostDay]', day[key as keyof CostDay])
            //         if (day[key as keyof CostDay]) {
            //             this.displayList[key].isDisplay = true;
            //         }
            //     }
            // }

            // if (this.displayList.budgeted.isDisplay && this.displayList.scheduled.isDisplay) {
            //     this.displayList.budgetedVSScheduled.isDisplay = true;
            // }
            // if (this.displayList.budgeted.isDisplay && this.displayList.sales.isDisplay) {
            //     this.displayList.budgetedVSSales.isDisplay = true;
            // }
            // if (this.displayList.budgeted.isDisplay && this.displayList.actual.isDisplay) {
            //     this.displayList.budgetedVSActuals.isDisplay = true;
            // }
            // if (this.displayList.actual.isDisplay && this.displayList.scheduled.isDisplay) {
            //     this.displayList.actualsVSScheduled.isDisplay = true;
            // }
            // if (this.displayList.sales.isDisplay && this.displayList.scheduled.isDisplay) {
            //     this.displayList.salesVSScheduled.isDisplay = true;
            // }
            // if (this.displayList.sales.isDisplay && this.displayList.actual.isDisplay) {
            //     this.displayList.salesVSActuals.isDisplay = true;
            // }

            // let countBase = 0;
            // for (let column of Object.values(this.displayList)) {
            //     console.log('column', column)
            //     if ((column as any).isDisplay) countBase++
            // }
            // console.log('count', countBase)
            //
            // let countCompare = 0;
            // // for (let column of Object.values(this.displayList)) {
            // //     console.log('column', column)
            // //     if ((column as any).isDisplay) countCompare++
            // // }
            //
            // displayDef = countBase + countCompare;
            //
            // this.displayN = displayDef;
        },

        toggleShow() {
            this.data = [];
            this.total = [];
            (this.$refs.ModalRef as any).toggleShow();
        },


        cancelModal() {
            this.data = [];
            this.total = [];
            // (this.$refs.ModalRef as any).toggleShow();
        }
    },
})
